"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Sidebar } from "@/components/layout/sidebar"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Loader2, Plus, X, Save, ArrowRight, Trash2, AlertCircle } from "lucide-react"
import { Alert, AlertDescription } from "@/components/ui/alert"

const API_BASE = process.env.NEXT_PUBLIC_API_URL || "http://127.0.0.1:8000"

export default function TransfersPage() {
  const router = useRouter()
  
  const [transfers, setTransfers] = useState<any[]>([])
  const [warehouses, setWarehouses] = useState<any[]>([])
  const [products, setProducts] = useState<any[]>([])
  const [stockList, setStockList] = useState<any[]>([])
  
  const [loading, setLoading] = useState(true)
  const [searchTerm, setSearchTerm] = useState("")
  const [statusFilter, setStatusFilter] = useState("all")

  const [isModalOpen, setIsModalOpen] = useState(false)
  const [isSaving, setIsSaving] = useState(false)
  const [error, setError] = useState("")

  const [fromWarehouse, setFromWarehouse] = useState("")
  const [toWarehouse, setToWarehouse] = useState("")
  const [lines, setLines] = useState<{product: string, quantity: number}[]>([])

  useEffect(() => {
    fetchData()
  }, [router])

  const fetchData = async () => {
    setLoading(true)
    try {
      const token = localStorage.getItem("token")
      if (!token) { 
        router.push("/")
        return
      }
      
      const headers = { Authorization: `Token ${token}` }

      const [transfersRes, warehousesRes, productsRes, stockRes] = await Promise.all([
        fetch(`${API_BASE}/api/transfers/`, { headers }),
        fetch(`${API_BASE}/api/warehouses/`, { headers }),
        fetch(`${API_BASE}/api/products/`, { headers }),
        fetch(`${API_BASE}/api/stock/`, { headers })
      ])

      if (transfersRes.ok) {
        const data = await transfersRes.json()
        console.log("Transfers from API:", data)
        setTransfers(Array.isArray(data) ? data : [])
      } else {
        console.error("Failed to fetch transfers")
        setTransfers([])
      }
      
      if (warehousesRes.ok) {
        const data = await warehousesRes.json()
        console.log("Warehouses from API:", data)
        setWarehouses(Array.isArray(data) ? data : [])
      } else {
        setWarehouses([])
      }
      
      if (productsRes.ok) {
        const data = await productsRes.json()
        console.log("Products from API:", data)
        setProducts(Array.isArray(data) ? data : [])
      } else {
        setProducts([])
      }
      
      if (stockRes.ok) {
        const data = await stockRes.json()
        console.log("Stock from API:", data)
        setStockList(Array.isArray(data) ? data : [])
      } else {
        setStockList([])
      }

    } catch (error) {
      console.error("Error fetching data:", error)
      setError("Failed to load data. Please refresh the page.")
      setTransfers([])
      setWarehouses([])
      setProducts([])
      setStockList([])
    } finally {
      setLoading(false)
    }
  }

  const getAvailableStock = (productId: string) => {
    if (!fromWarehouse || !productId) return 0
    const stockItem = stockList.find(
      s => s.product.toString() === productId && s.warehouse.toString() === fromWarehouse
    )
    return stockItem?.quantity || 0
  }

  const availableProducts = fromWarehouse 
    ? stockList.filter(s => s.warehouse.toString() === fromWarehouse && s.quantity > 0)
    : []

  const addLine = () => {
    if (!fromWarehouse) {
      alert("Please select source warehouse first")
      return
    }
    if (availableProducts.length === 0) {
      alert("No products available in selected warehouse")
      return
    }
    setLines([...lines, { product: "", quantity: 1 }])
  }

  const updateLine = (index: number, field: string, value: any) => {
    const newLines = [...lines]
    newLines[index] = { ...newLines[index], [field]: value }
    setLines(newLines)
  }

  const removeLine = (index: number) => {
    setLines(lines.filter((_, i) => i !== index))
  }

  const resetForm = () => {
    setFromWarehouse("")
    setToWarehouse("")
    setLines([])
    setError("")
  }

  const handleCreateTransfer = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    
    if (!fromWarehouse || !toWarehouse) {
      setError("Please select both source and destination warehouses.")
      return
    }

    if (lines.length === 0) {
      setError("Please add at least one item to transfer.")
      return
    }

    if (fromWarehouse === toWarehouse) {
      setError("Source and destination warehouses must be different.")
      return
    }

    for (const line of lines) {
      if (!line.product || line.quantity <= 0) {
        setError("All items must have a product selected and quantity greater than 0.")
        return
      }
      const available = getAvailableStock(line.product)
      if (line.quantity > available) {
        const productName = availableProducts.find(p => p.product.toString() === line.product)?.product_name || "Unknown"
        setError(`Insufficient stock for ${productName}. Available: ${available}, Requested: ${line.quantity}`)
        return
      }
    }

    setIsSaving(true)
    try {
      const token = localStorage.getItem("token")
      const headers = { 
        "Content-Type": "application/json",
        Authorization: `Token ${token}` 
      }

      const createRes = await fetch(`${API_BASE}/api/transfers/`, {
        method: "POST",
        headers,
        body: JSON.stringify({ 
          from_warehouse: parseInt(fromWarehouse), 
          to_warehouse: parseInt(toWarehouse), 
          status: "draft" 
        })
      })

      if (!createRes.ok) {
        const errorData = await createRes.json()
        throw new Error(errorData.error || "Failed to create transfer")
      }

      const transfer = await createRes.json()
      console.log("Created transfer:", transfer)

      for (const line of lines) {
        const itemRes = await fetch(`${API_BASE}/api/transfer-items/`, {
          method: "POST",
          headers,
          body: JSON.stringify({
            transfer: transfer.id,
            product: parseInt(line.product),
            quantity: line.quantity
          })
        })

        if (!itemRes.ok) {
          const errorData = await itemRes.json()
          throw new Error(errorData.error || "Failed to add transfer item")
        }
      }

      const validateRes = await fetch(`${API_BASE}/api/transfers/${transfer.id}/validate/`, {
        method: "POST",
        headers: { Authorization: `Token ${token}` }
      })

      if (validateRes.ok) {
        alert("Transfer created and validated successfully!")
        resetForm()
        setIsModalOpen(false)
        await fetchData()
      } else {
        const err = await validateRes.json()
        alert(`Transfer created but validation failed: ${err.error || "Unknown error"}`)
        resetForm()
        setIsModalOpen(false)
        await fetchData()
      }

    } catch (error: any) {
      setError(`Error: ${error.message || "Network error occurred"}`)
      console.error("Transfer creation error:", error)
    } finally {
      setIsSaving(false)
    }
  }

  const getWarehouseName = (id: number) => {
    const warehouse = warehouses.find(w => w.id === id)
    return warehouse?.name || `Warehouse #${id}`
  }

  const getProductName = (id: number) => {
    const product = products.find(p => p.id === id)
    return product?.name || `Product #${id}`
  }

  const filteredTransfers = transfers.filter((t) => {
    const searchLower = searchTerm.toLowerCase()
    const matchesSearch = 
      t.id.toString().includes(searchTerm) ||
      getWarehouseName(t.from_warehouse).toLowerCase().includes(searchLower) ||
      getWarehouseName(t.to_warehouse).toLowerCase().includes(searchLower)
    const matchesStatus = statusFilter === "all" || t.status === statusFilter
    return matchesSearch && matchesStatus
  })

  return (
    <div className="flex min-h-screen bg-background">
      <Sidebar />

      <main className="flex-1 ml-64 p-8 overflow-y-auto h-screen">
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-3xl font-bold tracking-tight text-foreground">Internal Transfers</h1>
            <p className="text-muted-foreground mt-1">Move stock between locations</p>
          </div>
          <Button onClick={() => setIsModalOpen(true)} className="bg-primary hover:bg-primary/90">
            <Plus className="mr-2 h-4 w-4" /> + New Transfer
          </Button>
        </div>

        {error && !isModalOpen && (
          <Alert variant="destructive" className="mb-4">
            <AlertCircle className="h-4 w-4" />
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <Card className="border-border">
          <CardContent className="pt-6">
            <div className="flex gap-4 mb-6">
              <div className="flex-1">
                <Input 
                  placeholder="Search transfers..." 
                  className="max-w-sm"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>
              <Select value={statusFilter} onValueChange={setStatusFilter}>
                <SelectTrigger className="w-[200px]">
                  <SelectValue placeholder="Filter by Status" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Statuses</SelectItem>
                  <SelectItem value="draft">Draft</SelectItem>
                  <SelectItem value="done">Completed</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="rounded-md border border-border">
              <Table>
                <TableHeader>
                  <TableRow className="bg-muted/50">
                    <TableHead className="font-semibold">Transfer ID</TableHead>
                    <TableHead className="font-semibold">Product</TableHead>
                    <TableHead className="font-semibold text-right">Qty</TableHead>
                    <TableHead className="font-semibold">From Location</TableHead>
                    <TableHead className="font-semibold">To Location</TableHead>
                    <TableHead className="font-semibold">Date</TableHead>
                    <TableHead className="font-semibold">Status</TableHead>
                    <TableHead className="font-semibold text-right">Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {loading ? (
                    <TableRow>
                      <TableCell colSpan={8} className="h-32 text-center">
                        <div className="flex flex-col items-center gap-2">
                          <Loader2 className="h-8 w-8 animate-spin text-primary" />
                          <p className="text-sm text-muted-foreground">Loading transfers...</p>
                        </div>
                      </TableCell>
                    </TableRow>
                  ) : filteredTransfers.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={8} className="h-32 text-center">
                        <div className="flex flex-col items-center gap-2">
                          <p className="text-muted-foreground font-medium">No transfers found</p>
                          <p className="text-sm text-muted-foreground">
                            {searchTerm || statusFilter !== "all" 
                              ? "Try adjusting your filters" 
                              : "Create your first transfer to get started"}
                          </p>
                        </div>
                      </TableCell>
                    </TableRow>
                  ) : (
                    filteredTransfers.map((transfer) => {
                      const firstItem = transfer.items?.[0]
                      const totalQty = transfer.items?.reduce((sum: number, item: any) => sum + item.quantity, 0) || 0
                      const itemCount = transfer.items?.length || 0
                      
                      return (
                        <TableRow key={transfer.id} className="hover:bg-muted/50">
                          <TableCell className="font-mono font-semibold">TRF-{String(transfer.id).padStart(3, '0')}</TableCell>
                          <TableCell>
                            {firstItem ? getProductName(firstItem.product) : 'No items'}
                            {itemCount > 1 && (
                              <span className="text-xs text-muted-foreground ml-2">
                                +{itemCount - 1} more
                              </span>
                            )}
                          </TableCell>
                          <TableCell className="text-right font-medium">{totalQty}</TableCell>
                          <TableCell className="text-muted-foreground">
                            {getWarehouseName(transfer.from_warehouse)}
                          </TableCell>
                          <TableCell className="text-muted-foreground">
                            {getWarehouseName(transfer.to_warehouse)}
                          </TableCell>
                          <TableCell className="text-muted-foreground">
                            {new Date(transfer.created_at).toLocaleDateString('en-US', {
                              year: 'numeric',
                              month: '2-digit',
                              day: '2-digit'
                            })}
                          </TableCell>
                          <TableCell>
                            <span
                              className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                                transfer.status === "done"
                                  ? "bg-green-100 text-green-800"
                                  : transfer.status === "draft"
                                  ? "bg-blue-100 text-blue-800"
                                  : "bg-yellow-100 text-yellow-800"
                              }`}
                            >
                              {transfer.status === 'done' ? 'Completed' : transfer.status === 'draft' ? 'In Progress' : 'Pending'}
                            </span>
                          </TableCell>
                          <TableCell className="text-right">
                            <Button 
                              variant="ghost" 
                              size="sm"
                              onClick={() => router.push(`/transfers/${transfer.id}`)}
                            >
                              View
                            </Button>
                          </TableCell>
                        </TableRow>
                      )
                    })
                  )}
                </TableBody>
              </Table>
            </div>
          </CardContent>
        </Card>
      </main>

      {isModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm">
          <div className="bg-white dark:bg-slate-900 rounded-lg shadow-lg w-full max-w-2xl border border-border p-6 max-h-[90vh] overflow-y-auto">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-bold">Create Internal Transfer</h2>
              <Button variant="ghost" size="icon" onClick={() => { setIsModalOpen(false); resetForm(); }}>
                <X className="h-4 w-4" />
              </Button>
            </div>

            {error && (
              <Alert variant="destructive" className="mb-4">
                <AlertCircle className="h-4 w-4" />
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            
            <div className="space-y-4">
              <div className="grid grid-cols-[1fr_auto_1fr] gap-4 items-end">
                <div className="space-y-2">
                  <Label>From (Source)</Label>
                  <Select value={fromWarehouse} onValueChange={(v) => { 
                    setFromWarehouse(v); 
                    setLines([]); 
                    setToWarehouse("");
                  }}>
                    <SelectTrigger><SelectValue placeholder="Select warehouse..." /></SelectTrigger>
                    <SelectContent>
                      {warehouses.length === 0 ? (
                        <SelectItem value="none" disabled>No warehouses available</SelectItem>
                      ) : (
                        warehouses.map(w => (
                          <SelectItem key={w.id} value={w.id.toString()}>{w.name}</SelectItem>
                        ))
                      )}
                    </SelectContent>
                  </Select>
                </div>
                
                <div className="pb-2">
                  <ArrowRight className="text-muted-foreground h-6 w-6" />
                </div>

                <div className="space-y-2">
                  <Label>To (Destination)</Label>
                  <Select value={toWarehouse} onValueChange={setToWarehouse} disabled={!fromWarehouse}>
                    <SelectTrigger><SelectValue placeholder="Select warehouse..." /></SelectTrigger>
                    <SelectContent>
                      {warehouses
                        .filter(w => w.id.toString() !== fromWarehouse)
                        .map(w => (
                          <SelectItem key={w.id} value={w.id.toString()}>{w.name}</SelectItem>
                        ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div className="border rounded-md p-4 bg-muted/10">
                <div className="flex justify-between items-center mb-3">
                  <Label>Items to Transfer</Label>
                  <Button 
                    type="button" 
                    size="sm" 
                    variant="outline" 
                    onClick={addLine}
                    disabled={!fromWarehouse || availableProducts.length === 0}
                  >
                    <Plus className="h-3 w-3 mr-1" /> Add Item
                  </Button>
                </div>
                
                {!fromWarehouse && (
                  <p className="text-sm text-muted-foreground mb-2">Select source warehouse first</p>
                )}

                {fromWarehouse && availableProducts.length === 0 && (
                  <Alert className="mb-2">
                    <AlertCircle className="h-4 w-4" />
                    <AlertDescription>No stock available in selected warehouse</AlertDescription>
                  </Alert>
                )}

                <div className="space-y-2 max-h-60 overflow-y-auto">
                  {lines.map((line, index) => {
                    const available = getAvailableStock(line.product)
                    return (
                      <div key={index} className="flex gap-2 items-start">
                        <div className="flex-1">
                          <Select 
                            value={line.product} 
                            onValueChange={(v) => updateLine(index, 'product', v)}
                          >
                            <SelectTrigger>
                              <SelectValue placeholder="Select product..." />
                            </SelectTrigger>
                            <SelectContent>
                              {availableProducts.length === 0 ? (
                                <SelectItem value="none" disabled>No products available</SelectItem>
                              ) : (
                                availableProducts.map(stockItem => (
                                  <SelectItem key={stockItem.id} value={stockItem.product.toString()}>
                                    {stockItem.product_name} (Avail: {stockItem.quantity})
                                  </SelectItem>
                                ))
                              )}
                            </SelectContent>
                          </Select>
                          {line.product && (
                            <p className="text-xs text-muted-foreground mt-1">
                              Available: {available} units
                            </p>
                          )}
                        </div>
                        <div className="w-28">
                          <Input 
                            type="number" 
                            placeholder="Quantity" 
                            min="1"
                            max={available || undefined}
                            value={line.quantity} 
                            onChange={(e) => {
                              const val = parseInt(e.target.value) || 1
                              updateLine(index, 'quantity', Math.min(val, available || val))
                            }} 
                          />
                        </div>
                        <Button 
                          type="button" 
                          variant="ghost" 
                          size="icon" 
                          className="text-destructive" 
                          onClick={() => removeLine(index)}
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    )
                  })}
                </div>
              </div>

              <div className="flex justify-end gap-2 pt-4">
                <Button type="button" variant="outline" onClick={() => { setIsModalOpen(false); resetForm(); }}>
                  Cancel
                </Button>
                <Button 
                  onClick={handleCreateTransfer}
                  disabled={isSaving || lines.length === 0 || !fromWarehouse || !toWarehouse} 
                  className="bg-primary"
                >
                  {isSaving ? (
                    <>
                      <Loader2 className="h-4 w-4 animate-spin mr-2" />
                      Processing...
                    </>
                  ) : (
                    <>
                      <Save className="h-4 w-4 mr-2" />
                      Create Transfer
                    </>
                  )}
                </Button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}